.preloader {
  z-index: 100;
  position: absolute;
  top: 0;
  left: 0;
  background: #fff;
  width: 100vw;
  height: 100vh;
  max-width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  &__info {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  & .icon {
    width: 100%;
    height: 100%;
    min-height: 20rem;
  }

  &__btn {
    position: relative;
    display: inline-block;
    color: #000;
    border: none;
    background-color: transparent;
    outline: none;
    cursor: pointer;

    &:hover {
      & .icon {
        transform: translateY(20%);
        transition: 1s;
        opacity: 1;
      }
    }

    & .icon {
      width: 100%;
      height: 100%;
      min-height: 5rem;
    }
  }
}
