.navbar {
    display: flex;
    flex-wrap: wrap;
    justify-content:flex-start;
    flex-wrap: nowrap;
    padding: 0 0.5rem;
    @media (min-width: 768px) {
        flex-wrap: nowrap;
    }
    &-brand {
        padding: 0;
        margin: 0;
        min-width: 5rem;
        overflow: hidden;
        & .svg-icon {
            &--logo {
                width: 5rem;
                height: 5rem;
                @media (min-width: 768px) {
                    width: 100%;
                    height: 100%;
                    min-height: 7rem;
                    margin-top: -20px;
                }
            }
        }
    }
    &-title {
        font-size: 1.1rem;

        @media (min-width: 767px) {
            padding: 0 1rem;
            font-size: 1.3rem;
        }

        @media (min-width: 1200px) {
            font-size: 1.75rem;
        }
    }
    &-langs {
        margin-right: 0;
        margin-left: auto;
    }
}
