// $baseFontLight: 'Avenir Next Cyr Light', 'Arial', 'Helvetica CY', sans-serif;

// colors
$white-color: #ffffff;
$contrast-color: #f5f5f5;
$primary-color: #42474d;

$red-color: #cd4141;
$blue-color: #e2f0fb;
$yellow-color: #fffeda;
$green-color: #31a428;
$gray-color: #8f8f8f;
$city-list-color: #052a56;
$placeholder-color: rgba(66, 71, 77, 0.5);
$modal-bg: rgba(66, 71, 77, 0.8);
$bg: #f5f7f8;

$transition-base: 0.3s;


// $contrast-color: $city-list-color;

// Size
$mainFontSize: 20px;
$mainLineHeight: 130%;

$borderRadius: 16px; // 16px
// $boxShadow: 8px 8px 16px rgba(73, 76, 79, 0.19), -8px -8px 10px #ffffff;

$boxShadowdark: 4px 4px 8px rgba(0, 0, 0, 0.25);
$boxShadow: $boxShadowdark;
$w-size: 1440px;

$xxxs: 420px;
$xxs: 480px;
$xs: 576px;
$xsm: 767px;
$sm: 768px;
$md: 992px;
$xl: 1200px;
$lg: 1300px;
$xxl: 1400px;
$xlg: 1600px;
$xxlg: 1920px;

:root {
  --btn-padding: 0.5rem;
  --bs-body-line-height: 130%;

  --nav-btn-pd: 19px;

  @media (min-width: $sm) {
    --nav-btn-pd: 20px;
  }
  @media (min-width: $xl) {
    --btn-padding: 0.7rem 1rem;
    --nav-btn-pd: 20px 24px 19px 24px;
    --bs-modal-border-radius: 14px;
  }
}

@keyframes animBg {
  from {
    transform: scale(1);
  }

  to {
    transform: scale(1.2);
  }
}
@keyframes animBgRev {
  from {
    transform: scale(1.2);
  }

  to {
    transform: scale(1);
  }
}
